import * as React from "react";

import { useStaticQuery, graphql, Link } from "gatsby";

import Card from "@mui/material/Card";
import CardHeader from "@mui/material/CardHeader";
import CardContent from "@mui/material/CardContent";
import Avatar from "@mui/material/Avatar";
import Typography from "@mui/material/Typography";
import Box from "@mui/material/Box";
import Container from "@mui/material/Container";
import Grid from "@mui/material/Grid";
import { useTheme } from "@mui/system";

import { GatsbyImage, getImage } from "gatsby-plugin-image";

export default function BlogSection() {
  const theme = useTheme();

  const data = useStaticQuery(graphql`
    query ($tag: String) {
      allMarkdownRemark(
        sort: { frontmatter: { date: DESC } }
        filter: { frontmatter: { category: { eq: $tag } } }
      ) {
        edges {
          node {
            excerpt(pruneLength: 100)
            frontmatter {
              date
              slug
              title
              category
              author
              image {
                childImageSharp {
                  gatsbyImageData(width: 100)
                }
              }
            }
            fields {
              readingTime {
                minutes
              }
            }
          }
        }
      }
    }
  `);
  const { allMarkdownRemark } = data;
  const { edges } = allMarkdownRemark;

  console.log(111, edges);

  return (
    <Container
      id="testimonials"
      sx={{
        pt: { xs: 4, sm: 12 },
        pb: { xs: 8, sm: 16 },
        position: "relative",
        display: "flex",
        flexDirection: "column",
        alignItems: "center",
        gap: { xs: 3, sm: 6 },
      }}
    >
      <Box
        sx={{
          width: { sm: "100%", md: "60%" },
          textAlign: { sm: "left", md: "center" },
        }}
      >
        <Typography component="h2" variant="h4" color="text.primary">
          Recent Posts
        </Typography>
      </Box>
      <Grid container spacing={2}>
        {edges.map((blogPost, index) => {
          const featuredImg = getImage(
            blogPost.node.frontmatter.image?.childImageSharp?.gatsbyImageData
          );

          return (
            <Grid
              item
              xs={12}
              sm={6}
              md={4}
              key={index}
              sx={{ display: "flex" }}
            >
              <Link
                to={
                  blogPost.node.frontmatter.category +
                  blogPost.node.frontmatter.slug
                }
                style={{
                  display: "block",
                  // alignItems: "center",
                  textDecoration: "none",
                }}
              >
                <Card
                  sx={{
                    display: "flex",
                    flexDirection: "column",
                    justifyContent: "space-between",
                    flexGrow: 1,
                    p: 1,
                  }}
                >
                  <CardContent>
                    <Typography variant="body2" color="text.secondary">
                      {blogPost.node.excerpt}
                    </Typography>
                  </CardContent>
                  <Box
                    sx={{
                      display: "flex",
                      flexDirection: "row",
                      justifyContent: "space-between",
                      pr: 2,
                    }}
                  >
                    <CardHeader
                      title={blogPost.node.frontmatter.title}
                      subheader={blogPost.node.frontmatter.author}
                    />
                    <GatsbyImage image={featuredImg} />
                  </Box>
                </Card>
              </Link>
            </Grid>
          );
        })}
      </Grid>
    </Container>
  );
}
